.triangle-down{
  	width: 35px;
  	position: relative;
	bottom: 8%;
	right: 0%;
	left: 0;
  	height: 35px;
  	margin: 4% auto 0 auto;
	border-right:3px solid #333;
	border-bottom:3px solid #333;
    -ms-transform: rotate(45deg); /* IE 9 */
    -webkit-transform: rotate(45deg); /* Chrome, Safari, Opera */
    transform: rotate(45deg);
}
.triangle-down:hover{
	cursor: pointer;
}

.triangle-up{
  	width: 35px;
  	position: relative;
	bottom: 0;
	right: 0%;
	left: 0;
  	height: 35px;
  	margin:4% auto 0 auto;
	border-left:3px solid #333;
	border-top:3px solid #333;
    -ms-transform: rotate(45deg); /* IE 9 */
    -webkit-transform: rotate(45deg); /* Chrome, Safari, Opera */
    transform: rotate(45deg);
}
.triangle-up:hover{
	cursor: pointer;
}

#scrollTop{
	position: fixed;
	right: 25px;
	bottom: 25px;
	background:$brandColor;
	color: #000;
	padding: 10px;
	text-decoration: none;
	border-radius: 5px;
	display: none;
	opacity: .75;
	cursor: pointer;
}
#scrollTop:hover{
	opacity: 1;
	box-shadow: 0 0 20px rgba(0,0,0,.8);
}

.meniu{
    position: fixed;
    top: 30px;
    left: 185px;
  }
  .meniu .menu-line{
    position: relative;
    width: 10px;
    background: $brandColor;
    float: left;
    left: 20px;
  }
  .meniu .menu-list{
    position: relative;
    float: left;
    width: 200px;
    left: 20px;
    margin: 0;
    text-align: left;
    padding: 0;
    list-style: none;
    display: none;
    background-color: #eee;
    
  }
  .meniu .menu-list li{
    width: 100%;
    position: relative;
    padding-left: 5px;
  }
  .meniu .menu-list li:hover{
  	background-color: $brandColor;
  }
  .meniu .menu-list li a{
    display: block;
    width: 100%;
    color: #333;
  }
  .meniu .menu-list li a:hover{
  	text-decoration: none;
  }
  .clear{
    clear: both;
  }
  .about h3{
  	margin-bottom: 0;
  }

  .nav-tabs>li.active>a, .nav-tabs>li.active>a:focus, .nav-tabs>li.active>a:hover{
  	background-color:$brandColor;
  	color:#000;
  }
  .nav-tabs>li>a{color:#000;}
  .nav-tabs>li>a:hover{
  	background-color:$brandColor;
  	color:#000;
  }
  @media screen and (max-width: 992px){
  	#scrollTop{
  		bottom:100px;
  	}
  	#masterclass-apply{
  		margin-bottom:20%;
  	}
  	#applyNowForm input{
  		clear: both;
  		position: relative;
  		z-index: 1;
  	}
  	.meniu .menu-line{
  		display: none;
  	}
  	.meniu .menu-list{
  		width: 100%;
  		height: 100%;
  		background-color: #fff;
  		overflow: auto;
  		left: 0;
  		text-align: center;
  	}
  	.meniu .menu-list li{
        top: 120px;
        padding: 2%;
  	}
  	.meniu{
  		width: 100%;
  		left: 0;
  		right: 0;
  		top: 0;
  		height: 100%;
  	}
  	.my-button{
  		position: fixed;
  		bottom: 0;
  		width: 100%;
  		height: 50px;
  		z-index: 55;
  	}
