.inputfile {
	width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: 1;
}
.inputfile + label {
    font-size: 1.25em;
    font-weight: 700;
    color: #000;
    padding: 1%;
    background-color: $brandColor;
}

.inputfile:focus + label,
.inputfile + label:hover {
    background-color:$applyHover;
}
.inputfile + label {
	cursor: pointer; /* "hand" cursor */
}

.apply-now{
    font-size: 1.25em;
    font-weight: 700;
    color: #000;
    padding:0 1%;
    border:none;
    border-radius: 0;
    background-color: $brandColor;
    display: inline-block;
}
.apply-now:hover{
	background-color:$applyHover;
	color:#000;
	border:none;
	outline: none;
}
.apply-now:focus, .apply-now:visited, .apply-now:active{
	background-color:$applyHover;
	color:#000;
	border:none;
	outline: none !important;
}
.apply-now-submit{
	margin: 0 auto;
	display: block;
	padding: 1%;
	width: 100%;
	border-radius: 0;
}
